import React, { useEffect } from 'react';
import styles from './image.module.sass';
import { ImageProps } from './image.model';
import LazyLoad from 'vanilla-lazyload';

const Image: React.FC<ImageProps> = ({ 
  imageBase, 
  originalWidth, 
  originalHeight, 
}) => {
  const webpSrcSet = `
    ${imageBase}-169w.webp 169w,
    ${imageBase}-338w.webp 338w,
    ${imageBase}-676w.webp 676w,
    ${imageBase}-1024w.webp 1024w
  `;
  const pngSrcSet = `
    ${imageBase}-169w.png 169w,
    ${imageBase}-338w.png 338w,
    ${imageBase}-676w.png 676w,
    ${imageBase}-1024w.png 1024w
  `;
  
  const normalSrc = `${imageBase}-1024w.png`;
  const mockSrc = `${imageBase}-mock.png`;

  useEffect(() => {
    const lazyLoadInstance = new LazyLoad({
      elements_selector: '.lazy'
    });
    lazyLoadInstance.update();
  });
  

  return (
    <picture className={styles.picture}>
      <source
        type="image/webp" 
        data-srcset={webpSrcSet}
      />
      <img
        alt="Article preview"
        className={styles.picture__img + " lazy"}
        src={mockSrc}
        data-src={normalSrc}
        data-srcset={pngSrcSet}
        width={originalWidth}
        height={originalHeight}
      />
    </picture>
  )
};

export default Image;
